@import url(//fonts.googleapis.com/earlyaccess/nanumpenscript.css);

.container {
  background-image: url("../../assets/images/Screen2.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 51%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-top: 75px;
  padding-bottom: 50px;
}

.container_copyright {
  background-image: url("../../assets/images/Screen4.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 51%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  padding-top: 75px;
  padding-bottom: 50px;
  overflow-y: scroll;
}

.container_ {
  background-image: url("../../assets/images/Screen1.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 51%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-top: 75px;
  padding-bottom: 50px;
}

.flex1 {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.flex2 {
  display: flex;
  flex: 2;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.button_big {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75vw;
  height: 15vh;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 3.3em;
  border-radius: 24px;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-background-color: transparent;
  color: "rgb(85, 50, 0)";
  background: linear-gradient(125deg, rgba(274, 223, 232, 0.5), rgba(175, 207, 232, 0.5));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button_big_ {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75vw;
  height: 17vh;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 6em;
  border-radius: 24px;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-background-color: transparent;
  color: "rgb(85, 50, 0)";
  background: linear-gradient(125deg, rgba(274, 223, 232, 0.5), rgba(175, 207, 232, 0.5));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button_small {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 61vw;
  height: 9.5vh;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2.1em;
  border-radius: 24px;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-background-color: transparent;
  color: "rgb(85, 50, 0)";
  background: linear-gradient(125deg, rgba(274, 223, 232, 0.5), rgba(175, 207, 232, 0.5));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button_small_original {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60vw;
  height: 8.5vh;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2.1em;
  border-radius: 24px;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-background-color: transparent;
  color: "rgb(85, 50, 0)";
  background: linear-gradient(125deg, rgba(274, 223, 232, 0.5), rgba(175, 207, 232, 0.5));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button_big:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}
.button_big_:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}
.button_small:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}
.button_small_original:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}
