.number {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17.5%;
  height: 50px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.5em;
  border-top-left-radius: 10px;
}

.clear {
  z-index: 3;
  position: absolute;
  top: 50px;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-bottom-right-radius: 10px;
}

.top_text_container {z-index: 3; position: absolute; top: 0; right: 0; display: flex; flex-direction: row; justify-content: space-around; align-items: center; width: 83%; height: 50px; border-top-right-radius: 12px;}
.top_text_item {display: flex; justify-content: center; align-items: center; width: 100%; height: 100%;}
.top_text1 {flex: 1; font-family: 'GenYoMinTW'; font-size: 1.1em; margin-top: 4px;}
.top_text2 {flex: 1.8; font-family: 'Nanum Pen Script', cursive; font-size: 1.25em;}
.top_text3 {flex: 1; font-family: 'Nanum Pen Script', cursive; font-size: 1.25em;}

.container {
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100% - 50px);
}

.flex_item {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
}

.flex_container {
  position: relative;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.border_bottom_left_round {border-bottom-left-radius: 10px;}
.border_bottom_right_round {border-bottom-right-radius: 10px;}

.sound {
  z-index: 3;
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 35px;
  height: 30px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.15em;
  letter-spacing: 3px;
  padding-left: 3px;
  border-top-left-radius: 10px;
}

.flex_container_bottom_round {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.text {
  position: absolute;
  font-family: 'GenYoMinTW';
  font-size: 5.5em;
  text-align: justify;
  margin-top: 0.2em;
}
