.container {display: flex; justify-content: center; align-items: center; width: 100%; height: 100%; border-bottom-left-radius: 12px; border-bottom-right-radius: 12px;}
.number {position: absolute; top: 0; left: 0; display: flex; justify-content: center; align-items: center; width: 18%; height: 50px; font-family: 'Nanum Pen Script', cursive; font-size: 1.6em; border-top-left-radius: 12px;}
.top_text_container {position: absolute; top: 0; right: 0; display: flex; flex-direction: row; justify-content: space-around; align-items: center; width: 82%; height: 50px; border-top-right-radius: 12px;}
.top_text_item {display: flex; justify-content: center; align-items: center; width: 100%; height: 100%;}
.top_text1 {flex: 1; font-family: 'GenYoMinTW'; font-size: 1.1em; margin-top: 4px;}
.top_text2 {flex: 1.8; font-family: 'Nanum Pen Script', cursive; font-size: 1.25em;}
.top_text3 {flex: 1; font-family: 'Nanum Pen Script', cursive; font-size: 1.25em;}

.flex {display: flex; flex: 1; width: 100%; height: 100%;}
.flex2 {flex: 1.5;}
.flex_center {justify-content: center; align-items: center;}
.column {flex-direction: column;}
.row {flex-direction: row;}
.Idiom13_text1 {font-family: 'GenYoMinTW_Heavy'; font-size: 2.2em;}
.Idiom13_text2 {font-family: 'Nanum Pen Script', cursive;font-size: 2.35em;}
.Idiom13_text3 {font-family: 'Nanum Pen Script', cursive;font-size: 1.25em; padding: 6px;}
.CT {font-family: 'GenYoMinTW_Heavy'; font-size: 2.6em;}
.NTB {font-family: 'Nanum Pen Script', cursive;font-size: 1.65em;}
.NT {font-family: 'Nanum Pen Script', cursive;font-size: 1.65em;}
.image_container {display: flex; justify-content: center; align-items: center; width: 100%; height: 100%;}
.image {width: 60px; height: 60px;}
.image_ {width: 90px; height: 90px;}
