.container {
  z-index: 999;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 85%;
  height: 70%;
  background-color: rgba(250, 250, 250, 1);
  border-radius: 18px;
  padding: 8px;
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.flex1 {
  display: flex;
  flex: 1;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
}

.button_homepage {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 210px;
  height: 85px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2em;
  color: #121212;
  background: linear-gradient(125deg, rgba(274, 223, 232, 1), rgba(175, 207, 232, 1));
  border-radius: 100px;
  letter-spacing: 6px;
  padding-left: 9px;
  outline: none;
  border: none;
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button_homepage:active {
  box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);
}

.flex2 {
  position: relative;
  display: flex;
  flex: 1.1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-top: 0.75px solid gray;
}

.top_text {
  position: absolute;
  left: 10px;
  top: 10px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.2em;
  color: #121212;
}

.profile_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
}

.profile_image {
  width: 140px;
  height: 140px;
  border-radius: 70%;
  border: 1px solid rgba(128, 128, 128, 0.6);
  margin-top: 10px;
  margin-bottom: 10px;
}

.profile_text {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2em;
  letter-spacing: 6px;
  color: #121212;
  margin-left: 9px;
}

.profile_text_span {
  color: rgb(255, 0, 0);
}
