.number {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18%;
  height: 50px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.6em;
  border-top-left-radius: 12px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.text1 {
  font-family: 'GenYoMinTW';
  font-size: 1.8em;
  text-align: center;
  padding-left: 8px;
  padding-right: 8px;
  white-space: pre-line;
  line-height: 35px;
}

.text2 {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2em;
  text-align: center;
  padding-left: 8px;
  padding-right: 8px;
  white-space: pre-line;
}

.text3 {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.45em;
  text-align: center;
  padding-left: 8px;
  padding-right: 8px;
  white-space: pre-line;
}
