@font-face {
  font-family: "GenYoMinTW";
  src: url("./assets/data/fonts/GenYoMinTW.ttf") format("truetype"), url("./assets/data/fonts/GenYoMinTW.woff") format("woff");
}
@font-face {
  font-family: "GenYoMinTW_Heavy";
  src: url("./assets/data/fonts/GenYoMinTW_Heavy.ttf") format("truetype"), url("./assets/data/fonts/GenYoMinTW_Heavy.woff") format("woff");
}

*, *:after, *:before {box-sizing: border-box}
body {margin: 0}
a {-webkit-tap-highlight-color: transparent !important; text-decoration: none}
.App {min-width: 320px}

.swiper-pagination-bullet {
  width: 8px !important;
  height: 8px !important;
  background-color: rgba(12, 12, 12, 0.75) !important;
  margin-bottom: 5px !important;
}
.swiper-pagination-bullet-active {
  width: 8px !important;
  height: 8px !important;
  background-color: rgba(12, 12, 12, 1) !important;
  margin-bottom: 5px !important;
}
.react-swipeable-view-container {
  width: 100%;
  height: 100%;
  border-bottom-left-radius: 12px !important;
  border-bottom-right-radius: 12px !important;
}
.MuiTabs-flexContainer {
  display: flex;
  flex: 1;
  justify-content: space-around;
}
.MuiTab-root {
  padding: 0 !important;
}

.swipeable_views_container {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: calc(100% - 50px);
  position: absolute;
  bottom: 0;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.swipeable_views_container_top_text {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: calc(100% - 100px);
  position: absolute;
  bottom: 0;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.tab_text {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.45em;
}

.modal_close {
  position: absolute;
  right: 8px;
  top: 8px;
}

.modal_left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translate(0, -50%);
  font-size: 42px !important;
  color: rgba(12, 12, 12, 0.8) !important;
}

.modal_right {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translate(0, -50%);
  font-size: 42px !important;
  color: rgba(12, 12, 12, 0.8) !important;
}
