@import url(//fonts.googleapis.com/earlyaccess/nanumpenscript.css);

.container {
  background-image: url("../../assets/images/Screen5.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 51%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-top: 75px;
  padding-bottom: 50px;
}

.flex1 {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 61vw;
  height: 11vh;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2.4em;
  border-radius: 24px;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-background-color: transparent;
  color: "rgb(85, 50, 0)";
  background: linear-gradient(125deg, rgba(274, 223, 232, 0.5), rgba(175, 207, 232, 0.5));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
}

.button:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}
