.number {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.6em;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.flex_container1 {
  display: flex;
  flex: 3.25;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.flex_container2 {
  display: flex;
  flex-direction: column;
  flex: 1.95;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.flex_container2_1 {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.flex_container2_2 {
  display: flex;
  flex: 0.95;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.text1 {
  font-family: 'GenYoMinTW_Heavy';
  font-size: 15.5em;
  text-align: center;
  margin-top: 0.2em;
}

.text2 {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2.3em;
  text-align: center;
}

.text3 {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 2.3em;
  text-align: center;
}

.review_container_light {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(274, 223, 232, 1), rgba(175, 207, 232, 1));
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.review_container_dark {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(22, 22, 22, 1);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.review_image {
  width: 90px;
  height: 90px;
}
