@import url(//fonts.googleapis.com/earlyaccess/nanumpenscript.css);

.container {
  background-image: url("../../assets/images/HomeBackground.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 51%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.text_container {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.text {
  background: linear-gradient(125deg, rgba(274, 223, 232, 1), rgba(175, 207, 232, 1));
  font-family: 'Nanum Pen Script', cursive;
  color: rgba(88, 88, 88, 1);
  font-size: 3.5em;
  padding: 20px 40px;
  border-radius: 100px;
  box-shadow: 3px 3px 5px 4px rgba(128, 128, 128, 0.7);
}

.semi_container {
  background-image: url("../../assets/images/HomeSemiBackground.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 52%;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50%;
  border-top-left-radius: 26px;
  border-top-right-radius: 26px;
}

.semi_image_container {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(125deg, rgba(274, 223, 232, 1), rgba(175, 207, 232, 1));
  font-family: 'Nanum Pen Script', cursive;
  color: rgba(88, 88, 88, 1);
  font-size: 3em;
  border-radius: 100px;
  padding: 20px 40px;
  box-shadow: 3px 3px 5px 4px rgba(128, 128, 128, 0.7);
}

.semi_image_container:active {
  box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);
}
.semi_image {
  width: 120px;
  height: 120px;
}
