.container {
  z-index: 999;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  width: 85%;
  height: 70%;
  background-color: rgba(250, 250, 250, 1);
  border-radius: 18px;
  padding: 8px;
}

.text {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.4em;
  color: #121212;
  padding: 0 8px;
}

.text_span {
  color: rgba(220, 0, 0, 1);
  margin-bottom: 6px;
}

.text_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1.35;
  border-top: 0.75px solid gray;
}

.text_copy_link_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 70%;
  background: linear-gradient(125deg, rgba(274, 223, 232, 1), rgba(175, 207, 232, 1));
  box-shadow: 2px 2px 3px 1.5px rgba(128, 128, 128, 0.7);
  border-radius: 100px;
  padding: 20px 0;
}

.text_copy_link_container:nth-child(1) {margin-bottom: 50px;}
.text_copy_link_container:hover {box-shadow: 0 0 0 0 rgba(128, 128, 128, 0.7);}

.text_copy {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.8em;
  color: #121212;
  margin-left: 10px;
}

.text_link {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 1.8em;
  color: #121212;
  margin-left: 8px;
}
